@import url('https://fonts.googleapis.com/css?family=Montserrat');

a.nav-link.disabled {
  color: #ddd !important;
  pointer-events: none;
}
li.nav-item.active > a.nav-link{
  text-decoration: underline;
}
.container {
margin-top: 20px;
font-family: 'Montserrat', sans-serif;
}

ul.xprag  > li.nav-item > a {
color: #aaa;
font-size: 90%;
font-family: "Unite", sans-serif;
}

ul.xprag  > li.nav-item.active > a {
color: #000;
}

h2 {
font-weight:700 !important;
letter-spacing: 0px !important;
}

h4 {
color: #777;
font-size: 120%;
}

p {
display: block;
  font-size: 16px;
  line-height: 1.75;
letter-spacing: 0.02em;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  color: #aaa;
}

ul > li {
  font-size: 16px;
letter-spacing: 0.02em;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  color: #aaa;
}

.media-body__right > h2{
  text-align: right;
}
.media-body__right > p {
  font-size: 9pt; 
  text-align:right
}